.cost_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem;
  background-color: rgba(36, 96, 110, 0.75);

  &__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
  }
  h2 {
    color: rgb(250, 250, 250);
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
  }
  &__price {
    font-size: 1rem;
    font-weight: bold;
    color: #fefae1;
    background-color: #2a5b84;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
  }
}
@media (min-width: 580px) {
  .cost_item {
    &__description {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 1;
    }

    h2 {
      font-size: 1.25rem;
    }

    &__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }
}
